import {Nav, Offcanvas, OffcanvasBody, OffcanvasHeader, OffcanvasTitle} from "react-bootstrap";
import {Link} from "react-router-dom";
import {
    Android2,
    FileBinaryFill, GearFill,
    HddRackFill,
    HouseDoorFill,
    InfoCircleFill,
    List,
    ListTask,
    PeopleFill, Windows
} from "react-bootstrap-icons";
import {useState} from "react";
import {observer} from "mobx-react-lite";
import {authState} from "../../store/auth";
import {userRole} from "../../store/model/IUser";
import {Simulate} from "react-dom/test-utils";
import wheel = Simulate.wheel;

export const LeftMenu = observer(() => {
    const [isShow, setShow] = useState(false);

    const setHide = () => setShow(false)

    return <>
        {/*Обычное меню - Начало*/}
        <div className="nav d-lg-flex d-none flex-column py-2 left-menu">
            <div className="mx-auto mt-2">
                <h4>LockVPN</h4>
            </div>
            <hr className="my-0 mt-2"/>
            <Nav className="flex-column">
                <Link className="nav-link d-flex justify-content-between align-items-center px-5 py-3" to="/">
                    <HouseDoorFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white">Главная</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between align-items-center px-5 py-3" to="/help">
                    <GearFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white">Настройки</span>
                </Link>
                <Link className="nav-link d-flex justify-content-between align-items-center px-5 py-3" to="/help">
                    <InfoCircleFill className="me-2" width={25} height={25} color="white"/>
                    <span className="w-100 text-center text-white">Помощь</span>
                </Link>
                <hr className="py-2 my-0"/>
                <div className="d-flex justify-content-around mx-4">
                    <div className="mx-1 my-auto border-1 border rounded-3 p-2">
                        <a href="https://appgallery.huawei.com/app/C110194213" className="text-decoration-none" target="_blank" rel="noreferrer">
                            <img src="./img/appgallery-icon.svg" alt="App Gallery" width={40} height={40} color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 my-auto border-1 border rounded-3 p-2">
                    <a href="https://4pda.to/forum/index.php?showtopic=1083324" className="text-decoration-none" target="_blank" rel="noreferrer">
                            <img src="./img/4pda_icon.svg" alt="App Gallery" width={40} height={40} color="white"/>
                        </a>
                    </div>
                    <div className="mx-1 my-auto border-1 border rounded-3 p-2">
                        <a className="not-hover text-black" href="https://lockvpn.me/files/windowsapp/LockVPNSetup.msi">
                            <Windows color="white" width={40} height={40}/>
                        </a>
                    </div>
                </div>
            </Nav>

            {authState.user?.role === userRole.admin &&
                <>
                    <hr/>
                    <Nav className="flex-column mx-4">
                    <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/users">
                            <PeopleFill className="me-2" color="white"/>
                            <span className="w-100 text-center text-white">Пользователи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center"
                              to="/admin/servers">
                            <HddRackFill className="me-2" color="white"/>
                            <span className="w-100 text-center text-white">Сервера</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/logs">
                            <ListTask className="me-2" color="white"/>
                            <span className="w-100 text-center text-white">Логи</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-between align-items-center" to="/admin/file">
                            <FileBinaryFill className="me-2" color="white"/>
                            <span className="w-100 text-center text-white">Файлы</span>
                        </Link>
                    </Nav>
                </>

            }
        </div>
        {/*Обычное меню - Конец*/}
    </>
})
